*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	scroll-behavior: smooth;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

body {
	font-weight: 400;
	font-size: 15px;
	line-height: 17px;
	letter-spacing: 0.06em;
	color: #000000;
	font-family: 'Arial';
	overflow: auto;
	&.lock {
		overflow: hidden;
	}
}

.container {
	max-width: 1280px;
	padding: 0 40px;
	margin: 0 auto;
	width: 100%;	
	@media (max-width: 320px) {
		// max-width: 300px;
		padding: 0 10px;
	}
}

.about__container {
	padding: 0px;
	display: flex;
	justify-content: center;
	align-items: center;
	// @media (max-width: 320px) {
	// 	padding: 0px 15px 0px 15px;
	// }
}

@media (max-width: 768px) {
	.pets__container {
		padding: 0px 30px;
	}
}

@media (max-width: 320px) {
	.footer__container {
		padding: 0px 10px 0px 10px;
	}
}

